import { VFC } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Formik } from 'formik';
import * as Yup from 'yup';
import { useHistory, useLocation, Link } from 'react-router-dom';
import { AppDispatch } from '../../../stores/app/store';
import {
  AuthFormWrapper,
  AuthFormHeading,
  TxField,
  ErrorMessage,
  LoaderWrapper,
  AuthFormBottomLink,
  AuthFormBottomLinkWrapper,
} from '../../atoms/Form/FormElements';

import AuthFormButton from '../../atoms/Buttons/AuthFormButton';
import Loading from '../../atoms/Loader';
import ErrorAlert from '../../atoms/Alert/ErrorAlert';
import InfoAlert from '../../atoms/Alert/InfoAlert';

import {
  fetchCredStart,
  fetchCredEnd,
  selectIsLoadingAuth,
  fetchAsyncLogin,
  selectAuthErrorMessage,
  resetAuthErrorMessage,
} from '../../../stores/slices/auth/authSlice';

import {
  setInfoMessage,
  setIsExistInfoMessage,
} from '../../../stores/slices/message/messageSlice';

export interface LOCATION_FROM_PROPS {
  from: string;
}

const LoginPage: VFC = () => {
  const isLoadingAuth = useSelector(selectIsLoadingAuth);
  const dispatch: AppDispatch = useDispatch();
  const history = useHistory();
  const { state } = useLocation<LOCATION_FROM_PROPS>();
  const authErrorMessages = useSelector(selectAuthErrorMessage);
  return (
    <Formik
      initialErrors={{ email: 'required' }}
      initialValues={{ email: '', password: '' }}
      onSubmit={async (values) => {
        dispatch(fetchCredStart());
        const result = await dispatch(fetchAsyncLogin(values));

        if (fetchAsyncLogin.fulfilled.match(result)) {
          dispatch(resetAuthErrorMessage());
          dispatch(setInfoMessage(''));
          dispatch(setIsExistInfoMessage());
          history.replace(state.from ?? '/');
        }
        dispatch(fetchCredEnd());
      }}
      validationSchema={Yup.object().shape({
        email: Yup.string()
          .email('')
          .required(''),
        password: Yup.string().required(''),
      })}
    >
      {({
        handleSubmit,
        handleChange,
        handleBlur,
        values,
        errors,
        touched,
        isValid,
      }) => (
        <AuthFormWrapper>
          {authErrorMessages.map((message) => (
            <ErrorAlert text={message} />
          ))}
          <form onSubmit={handleSubmit}>
            <div>
              <AuthFormHeading></AuthFormHeading>
              <br />

              <LoaderWrapper>{isLoadingAuth && <Loading />}</LoaderWrapper>
              <TxField
                id="standard-basic"
                variant="standard"
                placeholder=""
                type="input"
                name="email"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.email}
              />
              {touched.email && errors.email ? (
                <ErrorMessage>{errors.email}</ErrorMessage>
              ) : null}
              <TxField
                id="standard-basic"
                variant="standard"
                placeholder=""
                type="password"
                name="password"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.password}
              />
              {touched.password && errors.password ? (
                <ErrorMessage>{errors.password}</ErrorMessage>
              ) : null}
              <br />

              <AuthFormButton
                isLoading={isLoadingAuth}
                disabled={!isValid}
                ButtonText=""
              />
              <br />
              <AuthFormBottomLinkWrapper>
                <AuthFormBottomLink to="/password/reset">
                  
                </AuthFormBottomLink>
                <br />
                <AuthFormBottomLink to="/signup">
                  
                </AuthFormBottomLink>
              </AuthFormBottomLinkWrapper>
            </div>
          </form>
        </AuthFormWrapper>
      )}
    </Formik>
  );
};

export default LoginPage;
